#Profile {
    padding: 20px;
    display: flex;
    align-items: center;
    flex-direction: column;

    >table,
    >.player-controls {
        width: 100%;
        max-width: 522px;
        margin-bottom: 15px;
    }

    >.player-controls {
        text-align: right;
    }

    td.player-data {
        width: 50%;

        >table {
            width: 100%;

            tbody td {
                text-align: right;
                // padding-right: 5px;
                padding: 1px 5px;
            }
        }
    }

    .description {
        max-width: 261px;
        overflow: hidden;
        word-wrap: break-word;
    }

    .profile-edit {

        input,
        select,
        textarea {
            width: 100%;
            border: 1px solid green;
        }

        textarea {
            height: 100%;
            min-height: 160px;
            text-align: center;
        }

        td.second {
            position: relative;

            >div {
                position: absolute;
                left: 0px;
                top: 0px;
                width: 100%;
                height: 100%;
                padding: 1px;
            }
        }
    }

    button.edit {
        // height: 32px;
        width: 64px;
        font-weight: bold;
    }
}